@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@100;200;300;400;500;600;700&display=swap);
* {
  margin: 0 auto;
  box-sizing: border-box;
  padding: 0;
  font-family: 'Space Mono', monospace;
}

/* Remove arrows from <input> */
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}



.logo {
    margin-bottom: 2%;
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-color: hsl(185, 41%, 84%);
    height: 100vh;
    width: 100vw;  
}

@media only screen and (min-width: 880px) {
    .card {
        justify-content: center;
    }
}
    


.ammountHolder {
    display: grid;
    grid-template-areas: 
        'tip'
        'total'
        'reset'
    ;
    width: 80%;
    height: 80%;
    border-radius: 0.8rem;
    background-color: hsl(183, 100%, 15%);
    grid-row: 5/9;
}


@media only screen and (min-width: 880px) {

    .ammountHolder {
        grid-row: 1/5;
    }
}
    
.tipAmmount {
    grid-area: tip;
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));
    align-self: center;
    width: 100%;
}

.tipAmmount__person {
    margin-left: 0;
    padding-left: 1.25rem;
    width: 100%;
}

.tipAmmount__person h5 {
    color: hsl(0, 0%, 99%);
}

.tipAmmount__person h6 {
    color: hsl(0, 0%, 59%)
}

.tipAmmount__result {
    font-size: 1.5rem;
    font-weight: 600;
    color: hsl(172, 67%, 45%);
}

.total {
    grid-area: total;
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));
    align-self: flex-start;
    width: 100%;
}

.total__person {
    margin-left: 0;
    padding-left: 1.25rem;
    width: 100%;
}

.total__person h5 {
    color: hsl(0, 0%, 99%);
    margin-left: 0;
}

.total__person h6 {
    color: hsl(0, 0%, 59%)
}

.total__result {
    font-size: 1.5rem;
    font-weight: 600;
    color: hsl(172, 67%, 45%);
}

.btn {
    display: flex;
    align-items: center;
    grid-area: reset;
    width: 100%;
    height: 100%;
}

.btn__standard {
    width: 85%;
    height: 2rem;
    border-radius: 0.31rem;
    border: none;
    background-color: hsl(183, 80%, 24%);
    color: hsl(183, 100%, 15%);
    font-weight: 700;
    transition: 0.3s;
}

.btn__standard:hover {
    background-color: hsl(173, 61%, 77%);
    transition: 0.3s;
}

.btn__reset {
    width: 85%;
    height: 2rem;
    border-radius: 0.31rem;
    border: none;
    background-color: hsl(172, 67%, 45%);
    color: hsl(183, 100%, 15%);
    font-weight: 700;
}

@media only screen and (min-width: 880px) {
    .btn__standard {
        height: 3rem;
    }

    .btn__reset {
        height: 3rem;
    }
}
.bill {
    display: flex;
    width: 100%;
    height: 100%;
}

.bill__form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 80%;
    height: 100%;
}

.bill__header {
    font-size: 0.75rem;
    margin-left: 0;
    padding: 0 0 0.31rem 0;
    color:  hsl(0, 0%, 50%);
}

.bill__price {
    width: 100%;
    height: 2.5rem;
    border: none;
    border-radius: 0.31rem;
    text-align: right;
    padding-right: 0.31rem;
    font-size: 1.5rem;
    outline: none;
    transition: 0.5s;
    font-weight: 700;
    color: hsl(183, 100%, 15%);
    background: url(/Tip-Calc-App/static/media/icon-dollar.9f9be353.svg) no-repeat hsl(189, 41%, 94%) 8px;
}

.bill__price:focus {
    transition: 0.5s;
    box-shadow: inset 0 0 0 0.12rem hsl(172, 67%, 45%);
}
.selectTip {
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-row: 2/4;
    width: 100%;
    height: 100%;
}

.selectTip__header {
    font-size: 0.75rem;
    width: 80%;
    padding: 0 0 0.31rem 0;
    color: grey;
}

.selectTip__percent {
    display: grid;
    width: 80%;
    height: 50%;
    grid-template-columns: repeat(2,minmax(0,1fr));
    grid-column-gap: 0.6rem;
    grid-row-gap: 0.6rem;
}

.selectTip__style {
    width: 100%;
    height: 100%;
    border-radius: 0.31rem;
    border: none;
    color:  hsl(0, 0%, 99%);
    background-color: hsl(183, 100%, 15%);
    font-weight: 500;
    transition: 0.3s;
}

.selectTip__style__custom {
    width: 100%;
    height: 100%;
    border-radius: 0.31rem;
    border: none;
    color: hsl(183, 100%, 15%);
    background-color: hsl(189, 41%, 94%);
    font-weight: 500;
}

.custom__tip {
    width: 100%;
    height: 100%;
    border-radius: 0.31rem;
    border: none;
    box-shadow: inset 0 0 0 0.12rem hsl(172, 67%, 45%);
    color:  hsl(183, 100%, 15%);
    background-color: hsl(189, 41%, 94%);
    font-weight: 500;
    text-align: right;
    padding-right: 0.31rem;
    outline: none;
    outline-offset: -0.12rem;
}

.selectTip__style:hover{
    background-color: hsl(173, 61%, 77%);
    transition: 0.3s;
    color:  hsl(183, 100%, 15%);
}

.selectTip__style:focus{
    background-color: hsl(172, 67%, 45%);
    transition: 0.3s;
    color:  hsl(183, 100%, 15%);
}


@media (min-height: 300px) and (min-width: 300px) {

    .selectTip {
        grid-row: 2/4; 
    }

    .selectTip__percent {
        height: 50%;
        grid-column-gap: 0.1rem;
        grid-row-gap: 0.3rem;
    }
}
    

@media only screen and (min-width: 880px) {

    .selectTip {
        grid-row: 2/3; 
    }

    .selectTip__style {
        font-size: 1rem;
    }

    .custom__tip {
        font-size: 1rem;
    }

    .selectTip__percent {
        height: 50%;
        grid-template-columns: repeat(3,minmax(0,1fr));
        grid-template-rows: repeat(2,minmax(0,1fr));
        grid-column-gap: 0.3rem;
        grid-row-gap: 0.3rem;
    }
}
    


.people {
   display: flex;
   align-items: center;
   width: 100%;
   height: 100%;
}

.people__form {
    display: grid;
    grid-template-columns: repeat(2,minmax(0,1fr));
    width: 80%;
}

.people__label {
    font-size: 0.75rem;
    margin-left: 0;
    padding: 0 0 0.31rem 0;
    color: grey;
}

.people__label__hiden {
    margin-right: 0;
    grid-column: 2/3;
    font-size: 0.75rem;
    color: hsl(0, 0%, 99%);
    transition: 0.2s;
}

.people__label__zero {
    margin-right: 0;
    grid-column: 2/3;
    font-size: 0.75rem;
    color: hsl(0, 100%, 80%);
    transition: 0.2s;
}

.people__ammount {
    grid-column: 1/3; 
    width: 100%;
    height: 2.5rem;
    border: none;
    border-radius: 0.31rem;
    text-align: right;
    padding-right: 0.31rem;
    font-size: 1.5rem;
    outline: none;
    transition: 0.2s;
    font-weight: 700;
    color: hsl(183, 100%, 15%);
    background: url(/Tip-Calc-App/static/media/icon-person.65bfd204.svg) no-repeat hsl(189, 41%, 94%) 8px;
}

.people__ammount__warn{
    grid-column: 1/3; 
    width: 100%;
    height: 2.5rem;
    border: none;
    box-shadow: inset 0 0 0 0.125rem hsl(172, 67%, 45%);
    border-radius: 0.31rem;
    background: url(/Tip-Calc-App/static/media/icon-person.65bfd204.svg) no-repeat hsl(189, 41%, 94%) 8px;
    text-align: right;
    padding-right: 0.31rem;
    font-size: 1.5rem;
    outline: none;
    transition: 0.2s;
    font-weight: 700;
    color: hsl(183, 100%, 15%);
}

.people__ammount:focus {
    transition: 0.5s;
    box-shadow: inset 0 0 0 0.12rem hsl(172, 67%, 45%);
}
.border {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    grid-template-rows: repeat(6, minmax(0, 1fr));
    border-radius: 1.25rem;
    background-color: hsl(0, 0%, 99%);
    width: 100vw;
    height: 85vh;
    align-items: center;
}

@media only screen and (min-width: 880px) {
    .border {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        grid-template-rows: repeat(3, minmax(0, 1fr));
        width: 50vw;
        height: 40vh;
    }
}

@media only screen and (min-width: 1300px) {
    .border {
        width: 50vw;
    }
}

@media only screen and (min-width: 1900px) {
    .border {
        width: 30vw;
    }
}
    

    

